/**
 * TouchHandler - Detects and handles swipe gestures
 */
class TouchHandler {
  constructor(element, eventManager, config, onSwipe) {
    this.element = element;
    this.eventManager = eventManager;
    this.config = config;
    this.onSwipe = onSwipe;

    this.touchStartX = 0;
    this.touchStartY = 0;
    this.touchEndX = 0;
    this.touchEndY = 0;
    this.isEnabled = config.enableTouch;

    this.touchStartTime = 0;
    this.isDragging = false;

    this.boundTouchStart = this.handleTouchStart.bind(this);
    this.boundTouchMove = this.handleTouchMove.bind(this);
    this.boundTouchEnd = this.handleTouchEnd.bind(this);
  }

  /**
   * Enable touch handler
   */
  enable() {
    if (this.isEnabled) return;

    this.isEnabled = true;
    this.element.addEventListener('touchstart', this.boundTouchStart, { passive: false });
    this.element.addEventListener('touchmove', this.boundTouchMove, { passive: false });
    this.element.addEventListener('touchend', this.boundTouchEnd, { passive: false });
  }

  /**
   * Disable touch handler
   */
  disable() {
    if (!this.isEnabled) return;

    this.isEnabled = false;
    this.element.removeEventListener('touchstart', this.boundTouchStart);
    this.element.removeEventListener('touchmove', this.boundTouchMove);
    this.element.removeEventListener('touchend', this.boundTouchEnd);
  }

  /**
   * Handle touch start
   * @param {TouchEvent} event
   */
  handleTouchStart(event) {
    if (event.touches.length !== 1) return; // Only handle single touch

    this.touchStartX = event.touches[0].clientX;
    this.touchStartY = event.touches[0].clientY;
    this.touchStartTime = Date.now();
    this.isDragging = true;
    
    console.log('TouchHandler: Touch start at', this.touchStartX, this.touchStartY);
  }

  /**
   * Handle touch move
   * @param {TouchEvent} event
   */
  handleTouchMove(event) {
    if (!this.isDragging || event.touches.length !== 1) return;

    this.touchEndX = event.touches[0].clientX;
    this.touchEndY = event.touches[0].clientY;
    
    // Prevent default scrolling if it's a vertical swipe
    const deltaX = Math.abs(this.touchStartX - this.touchEndX);
    const deltaY = Math.abs(this.touchStartY - this.touchEndY);
    
    if (deltaY > deltaX) {
      // Vertical swipe - prevent page scroll
      event.preventDefault();
    }
  }

  /**
   * Handle touch end
   * @param {TouchEvent} event
   */
  handleTouchEnd(event) {
    if (!this.isDragging) return;

    this.isDragging = false;

    // Use changedTouches to get the final position
    const endX = event.changedTouches[0].clientX;
    const endY = event.changedTouches[0].clientY;

    const deltaX = this.touchStartX - endX;
    const deltaY = this.touchStartY - endY;
    const deltaTime = Date.now() - this.touchStartTime;

    console.log('TouchHandler: Touch end', {
      startX: this.touchStartX,
      startY: this.touchStartY,
      endX,
      endY,
      deltaX,
      deltaY,
      deltaTime
    });

    // Calculate velocity
    const velocity = Math.abs(deltaY) / deltaTime;

    // Check if it's a vertical swipe (not horizontal scroll) - Instagram Reels style
    if (Math.abs(deltaX) > Math.abs(deltaY)) {
      console.log('TouchHandler: Horizontal swipe detected, ignoring');
      return; // Horizontal scroll, ignore
    }

    // Check if swipe distance is significant
    if (Math.abs(deltaY) < this.config.swipeThreshold && velocity < 0.5) {
      console.log('TouchHandler: Swipe too small', { deltaY: Math.abs(deltaY), threshold: this.config.swipeThreshold, velocity });
      return; // Swipe too small
    }

    const direction = deltaY > 0 ? 'up' : 'down';
    console.log('TouchHandler: Valid swipe detected:', direction);

    this.eventManager.emit('swipeDetected', {
      direction,
      distance: Math.abs(deltaY),
      velocity,
    });

    if (this.onSwipe) {
      this.onSwipe(direction);
    }
  }

  /**
   * Destroy handler
   */
  destroy() {
    this.disable();
    this.onSwipe = null;
  }
}

if (typeof module !== 'undefined' && module.exports) {
  module.exports = TouchHandler;
}
